}

#headerOne {
  background-color: rgb(241, 236, 222);
  color: black;
  border: solid;
  border-width: 1px;
  border-color: rgb(238, 199, 172);
  text-align: center;
  z-index: 0;
}

.modal-header, .modal-footer{
  background-color: #F9F7E7;
}

.modal-body{
  background-color: white;
}

#scanner_input{
  width: 100%;
}

.card{
  border: solid;
  border-width: 1px;
  border-color: #D3D2D7;
  margin-bottom: 10px;
  background-color: white;
}

.card-block, .card-footer{
  padding: 25px;
}

.card-title{
  font-weight: bold;
}

.card-footer{
  border-top: solid;
  border-color: rgb(238, 199, 172);
  border-width: 1px;
}


.form-control, .form-group{
  width: 100%;
}

form {
  width: 100%;
}

